.loader {
    display: flex;
    text-align: center;
    justify-content: center;
    align-items: center;
    min-height: 80vh;

}

.ring {
    position: absolute;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    animation: ring 2s linear infinite;

}

@keyframes ring {
    0% {
        transform: rotate(0deg);
        box-shadow: 1px 3px 2px #e65c00;
    }

    50% {
        transform: rotate(180deg);
        box-shadow: 1px 3px 2px #18b201;
    }

    100% {
        transform: rotate(360deg);
        box-shadow: 1px 3px 2px #0456c8;
    }
}

.ring:before {
    position: absolute;
    content: '';
    left: 0;
    top: 0;
    height: 100%s;
    width: 100%;
    border-radius: 50%;
    box-shadow: 0 0 5px rgba(255, 255, 255, .3);
}

.loader-text {
    color: #737373;
    font-size: .7rem;
    text-transform: uppercase;
    letter-spacing: 1px;
    animation: text 3s ease-in-out infinite
}

@keyframes text {
    50% {
        color: #000;
    }
}